import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Helmet from "react-helmet";

//ì»´í¬ë„ŒíŠ¸
import Section from "Components/Section";
import Loader from "Components/Loader";
import Message from "Components/Message";
import Poster from "Components/Poster";

const HomePresenter = ({ nowPlaying, upcoming, popular, error, loading }) => (
    //ë§ˆìš´íŠ¸ê°€ ëë‚˜ê¸° ì „ê¹Œì§€ëŠ” nowPlaying, upcoming, popular ê°’ì´ ìƒì„±ë˜ì§€ ì•Šê¸° ë•Œë¬¸ì—
    //ë§ˆìš´íŠ¸ê°€ ëë‚˜ëŠ” ì‹œì ! ë§ ê·¸ëŒ€ë¡œ loadingì´ falseê°€ ë ë•Œ ìœ„ì˜ ê°’ì„ ì‚¬ìš©í•˜ë„ë¡ ì„¤ì •í•œë‹¤.
    //null ê°’ì„ ì‚¬ìš©í•˜ë©´ ì•„ë¬´ê²ƒë„ ë Œë”ë§í•˜ì§€ ì•ŠëŠ”ë‹¤.
    <>
        <Helmet>
            <title>Movies | Nomflix</title>
        </Helmet>
        {loading ? (
            <Loader />
        ) : (
            <Container>
                {nowPlaying && nowPlaying.length > 0 && (
                    <Section title="Now Playing">
                        {nowPlaying.map((movie) => (
                            <Poster
                                key={movie.id}
                                id={movie.id}
                                title={movie.title}
                                imageUrl={movie.poster_path}
                                rating={movie.vote_average}
                                isMovie={true}
                                year={
                                    movie.release_date &&
                                    movie.release_date.substring(0, 4)
                                }
                            ></Poster>
                        ))}
                    </Section>
                )}
                {upcoming && upcoming.length > 0 && (
                    <Section title="Upcoming Movies">
                        {upcoming.map((movie) => (
                            <Poster
                                key={movie.id}
                                id={movie.id}
                                title={movie.title}
                                imageUrl={movie.poster_path}
                                rating={movie.vote_average}
                                isMovie={true}
                                year={
                                    movie.release_date &&
                                    movie.release_date.substring(0, 4)
                                }
                            ></Poster>
                        ))}
                    </Section>
                )}
                {popular && popular.length > 0 && (
                    <Section title="popular Movies">
                        {popular.map((movie) => (
                            <Poster
                                key={movie.id}
                                id={movie.id}
                                title={movie.title}
                                imageUrl={movie.poster_path}
                                rating={movie.vote_average}
                                isMovie={true}
                                year={
                                    movie.release_date &&
                                    movie.release_date.substring(0, 4)
                                }
                            ></Poster>
                        ))}
                    </Section>
                )}
                {error && <Message text={error} />}
            </Container>
        )}
    </>
);

HomePresenter.propTypes = {
    nowPlaying: PropTypes.array,
    upcoming: PropTypes.array,
    popular: PropTypes.array,
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
};

const Container = styled.div`
    padding: 20px;
`;

export default HomePresenter;
